
import React, {Component} from 'react';

class Projects extends Component {

    state = {
        projs: []
    }

    componentDidMount(){
        const url = 'https://api.github.com/users/apguilherme/repos';
        fetch(url)
            .then(response => response.json())
            .then(json => {
                this.setState({ projs: json });
                console.log(this.state.projs);
            });
    }

    render(){
        
        return (
            <div className=''>

                <p className="topic"><span className='letter'>projects</span></p>
                <p><i>*this data was retrieved using the Github public API.</i></p>

                <div className="card-columns">
                    
                    { this.state.projs.map(item => (

                            <div key={item.id} className="card">
                                <div className="card-body">
                                    <p className="card-title text-left"><b>{item.name}</b></p>
                                    <p className="card-text text-right">{item.description}</p>
                                
                                    <div className="alinha">
                                        <a href={item.html_url} className="btn btn-outline-dark" rel="noopener noreferrer" target='_blank'>See code</a>
                                        <small><i className="fa fa-desktop" aria-hidden="true"></i> {item.language}</small>
                                        <small><i className="fa fa-clock-o"></i> {item.created_at.split('T')[0].split('-')[0]}</small>
                                    </div>
                                </div>
                            </div>
                    )) }
                    
                </div>

            </div>
        )
    }
}

export default Projects;
